export function createDefaultProductPreview() {
    return {
        title: "",
        thumbnail: "",
        date_created: "",
        tags: [],
        price: 499.99,
        rating: 0,
        reviews: 0,
        id: 0,
    };
}
export function createDefaultProduct() {
    return Object.assign(Object.assign({}, createDefaultProductPreview()), { description: "", details: [], images: [], key_points: [], reviews: [], related_products: [], is_out_of_stock: false, is_upcoming: false });
}
export function createDefaultStarRater() {
    return {
        rating: 0,
        reviews: 0,
        stars_1: 0,
        stars_2: 0,
        stars_3: 0,
        stars_4: 0,
        stars_5: 0,
    };
}
export function createDefaultUser() {
    return {
        id: 0,
        username: "",
        profile: "",
        date_created: "",
    };
}
